<template>
  <div class="con">
    <publicTop></publicTop>
    <!-- 头部 -->
    <div
      class="head-bg"
      :style="`background-image: url(${url});background-size: 100% 100%;`"
    >
      <!-- <img src="@/assets/Mask.png" alt="" class="head-bg-img" /> -->

      <div class="head-bg-t">
        <div class="head-bg-l" @click="goBack">
          <img src="@/assets/icon-back-white.png" alt="" />
        </div>

        <div class="head-bg-text">宣讲会</div>
      </div>
      <div class="head-bg-ending">
        <div class="zhao-head-div">
          <input
            type="text"
            v-model="inputContent"
            placeholder="请输入公司/职位"
          />
          <img src="@/assets/icon-search.png" alt="" @click="goSear" />
        </div>
        <div class="head-bg-ending-r" @click="goShow">
          <div class="head-bg-ending-r-text">类型</div>
          <img src="@/assets/icon_d.png" alt="" />
        </div>
      </div>
    </div>
    <!-- 中部 -->
    <!-- <div class="footer-top">
      <div class="footer-top-l">
        <div
          v-for="(item, index) in tabArray"
          :key="index"
          @click="goToggle(index)"
          class="footer-top-l-title"
          :class="tabIndex == index ? 'actived' : ''"
        >
          {{ item.name }}
        </div>
      </div>
      <div class="footer-top-r">
        <div class="footer-top-r-l" @click="goCity">
          <div class="footer-top-r-l-text">地区</div>
          <img src="@/assets/icon_d.png" alt="" />
        </div>
        <div class="footer-top-r-r" @click="goSelect">
          <div class="footer-top-r-l-text">筛选</div>
          <img src="@/assets/icon_d.png" alt="" />
        </div>
      </div>
    </div> -->

    <!-- 底部列表 -->
    <div class="list-all" v-if="allL.length != 0">
      <van-list
        :immediate-check="false"
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        loading-text="下拉查看全部"
        @load="kh"
      >
        <div
          v-for="(item, index) in allL"
          :key="index"
          class="list-all-li"
          @click="goDe(item.id)"
        >
          <!-- <img src="@/assets/ik.png" alt="" class="list-all-li-ab" /> -->
          <!-- <div class="list-all-li-ab1">今天</div> -->
          <!-- 分线上线下 -->
          <img
            src="@/assets/status-down.png"
            alt=""
            class="list-all-li-status"
            v-if="item.fairType == 1 || item.fairType == 2"
          />
          <img
            src="@/assets/status-up.png"
            alt=""
            class="list-all-li-status"
            v-else
          />
          <div class="list-all-li-l">
            <img src="@/assets/momo.png" alt="" />
          </div>
          <div class="list-all-li-r">
            <div class="list-all-li-r-t">
              {{ item.title }}
            </div>
            <div class="list-all-li-r-m">
              <img src="@/assets/gray.png" alt="" class="list-all-li-r-m-l" />
              <div class="list-all-li-r-m-r">{{ item.place }}</div>
            </div>
            <div class="list-all-li-r-r">
              <img src="@/assets/timehei.png" alt="" />
              <div class="list-all-li-r-r-text">
                {{ item.startTime.slice(0, 10) }}
              </div>
            </div>
          </div>
        </div>
      </van-list>
    </div>
    <!-- 没有数据时 -->
    <div class="list-all-no" v-if="allL.length == 0">
      <img src="@/assets/noData.png" alt="" class="list-all-no-img" />
      <div class="list-all-no-text">暂无数据</div>
    </div>
    <!-- 类型弹框 -->
    <van-popup v-model="isShow" position="bottom" v-if="chooseList.length != 0">
      <div
        class="pop-div"
        v-for="(item, index) in chooseList"
        :key="index"
        :class="[
          index == chooseList.length - 1 ? 'pop-div1' : '',
          iIndex == index ? 'acti' : '',
        ]"
        @click="goChoose(index, item.value)"
      >
        {{ item.label }}
      </div>
      <div class="pop-line"></div>
      <div class="pop-end" @click="goClose">取消</div>
    </van-popup>
    <HomeTop :i="checkIndex" />
  </div>
</template>

<script>
import qs from "qs";
import HomeTop from "@/components/homeTop.vue";
import publicTop from "@/components/publicTop.vue";
export default {
  name: "recruitmentFairList",
  components: {
    HomeTop,
    publicTop,
  },
  data() {
    return {
      checkIndex: 2,
      tabIndex: 0,
      tabList: [
        { name: "招聘信息" },
        { name: "实习信息" },
        { name: "双选会" },
        { name: "宣讲会" },
      ],
      inputContent: "",
      tabArray: [{ name: "推荐" }, { name: "最新" }],
      url: require("../assets/Mask.png"),
      loading: false,
      finished: false,
      pageNo: 1,
      allL: [],
      bgc: this.GLOBAL.bgc,
      isShow: false,
      online: "",
      chooseList: [],
      iIndex: 9999999999,
    };
  },
  beforeRouteLeave(to, from, next) {
    console.log(to, from);
    if (to.path === "/recruitmentFairForm") {
      this.$store.commit("updataListXuan", this.allL);
      this.$store.commit("updataPageXuan", this.pageNo);
      this.$store.commit("updataListXuanType", this.chooseList);
    } else {
      this.$store.commit("updataListXuan", []);
      this.$store.commit("updataPageXuan", "");
      this.$store.commit("updataInputXuan", "");
      // this.$store.commit("updataLineXuan", "");
      sessionStorage.setItem("LineXuan", "");
      // this.$store.commit("updataIIndex1", "");
      sessionStorage.setItem("IIndex1", "");
    }
    next();
  },
  watch: {
    inputContent(newV, oldV) {
      console.log(newV, oldV);
      this.$store.commit("updataInputXuan", newV);
    },
  },
  methods: {
    goSear() {
      console.log(this.inputContent, "search");
      // this.$router.go(0);
      this.$store.commit("updataInputXuan", this.inputContent);
      this.allL = [];
      this.kh();
    },
    goBack() {
      this.$router.go(-1);
    },
    goDe(id) {
      // this.$store.commit("updataDetailId", id);
      // this.$router.push({ name: "xuanDetail" });

      this.$router.push({
        path: "/recruitmentFairForm",
        query: { type: 3, id: id },
      });
    },
    goShow() {
      this.isShow = !this.isShow;
    },
    goChoose(index, num) {
      console.log(num);
      this.iIndex = index;
      this.online = num;
      this.isShow = false;
      this.allL = [];
      this.kh();
      // this.$store.commit("updataLineXuan", this.online);
      sessionStorage.setItem("LineXuan", this.online);
      // this.$store.commit("updataIIndex1", this.iIndex);
      sessionStorage.setItem("IIndex1", this.iIndex);
      console.log(this.online, "online");
    },
    goClose() {
      this.isShow = false;
      // this.$store.commit("updataLineXuan", "");
      sessionStorage.setItem("LineXuan", "");
      // this.$store.commit("updataIIndex1", "");
      sessionStorage.setItem("IIndex1", "");
      this.$router.go(0);
    },
    // goCity() {
    //   this.$router.push({ name: "zhaoMoreCity" });
    // },
    // goSelect() {
    //   this.$router.push({ name: "zhaoMoreSelect" });
    // },
    goToggle(index) {
      this.tabIndex = index;
    },
    kh() {
      let that = this;
      that.loading = true;
      console.log(that.$store.getters.getPageXuan, "页数");
      if (that.$store.getters.getPageXuan) {
        that.pageNo = that.$store.getters.getPageXuan;
      }
      if (that.pageNo == 1) {
        that.allL = [];
      }
      if (that.allL.length == 0) {
        that.pageNo = 1;
      }
      const params = {
        pageNo: that.pageNo,
        pageSize: 16,
        fairType: that.online,
        title: that.inputContent,
      };
      console.log(params);
      that.$axios
        // .get(
        //   "/f/recruitmentFair/ajax_frontRecruitfair?pageNo=" +
        //     that.pageNo +
        //     "&pageSize=16" +
        //     "&fairType=" +
        //     that.online +
        //     "&title=" +
        //     that.inputContent
        // )
        .post("/f/recruitmentFair/ajax_frontRecruitfair", qs.stringify(params))
        .then((res) => {
          console.log(res, "数据list");
          if (res.data.state == 1) {
            that.loading = false;
            if (that.pageNo <= res.data.object.totalPage) {
              ++that.pageNo;
              that.$store.commit("updataPageXuan", that.pageNo);
              if (res.data.object.list && res.data.object.list.length != 0) {
                that.allL = [...that.allL, ...res.data.object.list];
              }
              console.log(that.allL, that.allL.length, 56);
            } else {
              console.log("有吗");
              that.$store.commit("updataPageXuan", "");
              that.finished = true;
            }

            if (
              res.data.object.searchMap &&
              res.data.object.searchMap.fairType &&
              res.data.object.searchMap.fairType.length != 0
            ) {
              that.chooseList = res.data.object.searchMap.fairType;
            }
          } else {
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
  },
  created() {
    console.log(
      this.allL.length,
      this.$store.getters.getPageXuan,
      typeof this.$store.getters.getPageXuan,
      "all",
      sessionStorage.getItem("LineXuan"),
      sessionStorage.getItem("IIndex1")
    );
    if (this.$store.getters.getInputXuan) {
      this.inputContent = this.$store.getters.getInputXuan;
      console.log(this.inputContent);
    } else {
      this.inputContent = "";
    }
    if (
      sessionStorage.getItem("LineXuan") == "null" ||
      sessionStorage.getItem("LineXuan") == "" ||
      sessionStorage.getItem("LineXuan") == null
    ) {
      this.online = "";
    } else {
      this.online = sessionStorage.getItem("LineXuan");
      console.log(this.online);
    }

    if (
      sessionStorage.getItem("IIndex1") == "null" ||
      sessionStorage.getItem("IIndex1") == "" ||
      sessionStorage.getItem("IIndex1") == null
    ) {
      this.iIndex = 99999999;
    } else {
      this.iIndex = sessionStorage.getItem("IIndex1");
      console.log(this.iIndex);
    }

    if (this.$store.getters.getListXuan.length != 0) {
      this.allL = this.$store.getters.getListXuan;
      this.chooseList = this.$store.getters.getListXuanType;
      console.log(this.allL, this.chooseList, "all");
    } else {
      this.kh();
    }
  },
  beforeDestory() {},
  destroyed() {},
  mounted: function () {},
};
</script>
<style lang="css" scoped>
.con {
  background-color: #f5f5f5;
  padding-bottom: 1.66rem;
  min-height: 100%;
}
/* 类型弹框 */
.pop-div {
  width: 7.5rem;
  height: 0.98rem;
  border-bottom: 0.02rem solid #eeeeee;
  text-align: center;
  line-height: 0.98rem !important;
  font-family: PingFangSC-Regular;
  font-size: 0.32rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #3f4967;
}
.acti {
  color: #4760d7 !important;
}
.pop-div1 {
  width: 7.5rem;
  height: 0.98rem;
  text-align: center;
  line-height: 0.98rem !important;
  font-family: PingFangSC-Regular;
  font-size: 0.32rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #3f4967;
  border: none;
}
.pop-line {
  width: 7.5rem;
  height: 0.14rem;
  background-color: #f5f5f5;
}
.pop-end {
  width: 7.5rem;
  height: 0.98rem;
  text-align: center;
  line-height: 0.98rem !important;
  font-family: PingFangSC-Regular;
