  height: 0.24rem;
}
.head-bg-ending-r {
  width: 0.98rem;
  height: 0.72rem;
  background-color: #ffffff;
  border-radius: 0.36rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.head-bg-ending-r-text {
  font-family: PingFangSC-Medium;
  font-size: 0.24rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: -0.01rem;
  color: #6f768d;
}
.head-bg-ending-r img {
  width: 0.14rem;
  height: 0.08rem;
  margin-left: 0.1rem;
}
.footer-top {
  width: 7.5rem;
  height: 1rem;
  padding: 0 0.32rem;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer-top-l {
  width: 3rem;
  height: 1rem;
  display: flex;
  align-items: center;
}

.footer-top-r {
  height: 1rem;
  display: flex;
  align-items: center;
}

.footer-top-r-l {
  height: 0.5rem;
  background-color: #f0f0f0;
  border-radius: 0.04rem;
  margin-right: 0.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 0.1rem;
  box-sizing: border-box;
}

.footer-top-r-l img {
  width: 0.14rem;
  height: 0.08rem;
}

.footer-top-r-l-text {
  font-family: PingFangSC-Medium;
  font-size: 0.24rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: -0.01rem;
  color: #7a7c8b;
  margin-right: 0.05rem;
}

.footer-top-r-r {
  width: 0.94rem;
  height: 0.5rem;
  background-color: #f0f0f0;
  border-radius: 0.04rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-top-r-r img {
  width: 0.14rem;
  height: 0.08rem;
}
.footer-top-l-title {
  font-family: SourceHanSansCN-Regular;
  font-size: 0.28rem;
  font-weight: normal;
  letter-spacing: -0.01rem;
  color: #7a7c8b;
  margin-right: 0.5rem;
}
.actived {
  font-family: SourceHanSansCN-Medium;
  font-size: 0.3rem !important;
  font-weight: bold;
  font-stretch: normal;
  letter-spacing: -0.01rem;
  color: #2b2e34 !important;
}

.list-all-no {
  width: 7.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 0.32rem;
  padding-top: 0.3rem;
  box-sizing: border-box;
}
.list-all-no-img {
  width: 3.34rem;
  height: 1.76rem;
  margin-bottom: 0.7rem;
}
.list-all-no-text {
  font-family: PingFangSC-Regular;
  font-size: 0.32rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
