}

.list-all {
  width: 7.5rem;
  padding: 0 0.32rem;
  box-sizing: border-box;
}
.list-all-li {
  width: 6.86rem;
  background-color: #ffffff;
  border-radius: 0.15rem;
  margin-bottom: 0.2rem;
  display: flex;
  padding: 0.4rem 0.32rem;
  box-sizing: border-box;
  position: relative;
}
.list-all-li-status {
  position: absolute;
  top: 0;
  right: 0;
  width: 0.74rem;
  height: 0.68rem;
}
.list-all-li-ab {
  position: absolute;
  right: 0.32rem;
  top: 0.36rem;
  width: 0.36rem;
  height: 0.36rem;
}
.list-all-li-ab1 {
  position: absolute;
  font-family: PingFangSC-Regular;
  font-size: 0.22rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #c1c4ce;
  right: 0.32rem;
  bottom: 0.36rem;
}
.list-all-li-l {
  width: 0.8rem;
  height: 0.8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 0.24rem;
}
.list-all-li-l img {
  width: 0.8rem;
  height: 0.8rem;
}
.list-all-li-r {
  width: 4.7rem;
}
.list-all-li-r-t {
  width: 4.7rem;
  font-family: PingFangSC-Medium;
  font-size: 0.34rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #0f1b41;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  word-break: break-all;
  text-align: left;
}
.list-all-li-r-m {
  display: flex;
  align-items: center;
  margin-top: 0.1rem;
  margin-bottom: 0.1rem;
}
.list-all-li-r-m-l {
  width: 0.3rem;
  height: 0.34rem;
  margin-right: 0.1rem;
}
.list-all-li-r-m-r {
  font-family: PingFangSC-Medium;
  font-size: 0.24rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #999ca8;
  width: 4.2rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
